import React from 'react';
// import Nevbar from './nevbar';
import Coin from './coin';
import socketIOClient from "socket.io-client";
var socket;
// const endpoint = 'http://localhost:8080/';
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      price:'0',
      data:[],
      coin:'40'
    };
    // socket = socketIOClient(endpoint);
   
   
  }
  select=(data)=>{
      fetch("https://www.zebapi.com/pro/v1/market")
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({data : result[data.target.value]
              ,coin:data.target.value
  
          });
        }
      )
   
  }
  refresh=()=>{fetch("https://www.zebapi.com/pro/v1/market")
  .then(res => res.json())
  .then(
    (result) => {
      this.setState({data : result[this.state.coin]

      });
    })

  }
  componentDidMount() {
    fetch("https://www.zebapi.com/pro/v1/market")
  .then(res => res.json())
  .then(
    (result) => {
      this.setState({data : result[this.state.coin]

      });
    })
      
  }
render(){
  return (
    <div>
      {/* <Nevbar/> */}
      <select onChange={this.select}>
  <option value="40">xrp</option>
  <option value="19">btc</option>
</select>
<button onClick={this.refresh}>refresh</button>
      <Coin name={this.state.data}/>
    </div>
  );}
}

export default App;
